<template>
  <TopNav class="fixed-top" />

  <transition name="fade">
    <LoginPage
      @goJoin="mainImgFlag = false; loginFlag = false; "
      @closeLogin="loginFlag = false"
      v-if="loginFlag"
    />
  </transition>

  <div class="body_style pt-5">
      <MainImg v-if="mainImgFlag" />   
  </div>

   
     <div>
       <transition name="fade">
         <JoinPage @closeJoin="mainImgFlag = true" v-if="mainImgFlag == false" />
      </transition>
    </div>
  
  <transition name="fade">
    <div class="footer_option fixed-bottom" v-if="mainImgFlag">
      <button @click="goLogin" type="button" class="btn btn-outline-secondary">
        Login
      </button>
      <button type="button" class="btn btn-outline-secondary">+</button>
      <button type="button" class="btn btn-outline-secondary">Invite</button>
    </div>
  </transition>
</template>

<script>
import TopNav from "./components/TopNav.vue";
import MainImg from "./components/MainImg.vue";
import LoginPage from "./components//LoginPage.vue";
import JoinPage from "./components/JoinPage.vue";
export default {
  name: "App",
  data() {
    return {
      loginFlag: false,
      mainImgFlag: true,
    };
  },

  components: {
    TopNav: TopNav,
    MainImg: MainImg,
    LoginPage: LoginPage,
    JoinPage: JoinPage,
  },
  methods: {
    goLogin() {
      if (this.loginFlag == false) {
        this.loginFlag = true;
      } else {
        this.loginFlag = false;
      }
    },
  },
};
</script>

